import dsbridge from "./dsbridge";
const module_names = new Set([]);
const patch = {};

function isFunction(functionToCheck) {
  return (
    functionToCheck && {}.toString.call(functionToCheck) === "[object Function]"
  );
}

function isObject(val) {
  if (val === null) {
    return false;
  }
  return typeof val === "function" || typeof val === "object";
}

function isString(x) {
  return Object.prototype.toString.call(x) === "[object String]";
}

function isHybrid() {
  return window && window._dswk === true;
}
let xengine = {
  patch: patch,
  platform: platform(),
  hybrid: true,
  isHybrid: isHybrid,
  bridge: dsbridge,
  use: use,
  api: api,
  broadcastOn: broadcastOn,
  broadcastOff: broadcastOff,
  assert: xassert,
};

function xassert(targetID, expression) {
  if (expression) {
    document.getElementById(targetID).style.backgroundColor = "green";
  } else {
    document.getElementById(targetID).style.backgroundColor = "red";
  }
}

function api(jsimoduleId, funcname, args, cb) {
  if (args) {
    if (args.hasOwnProperty("__event__")) {
      only_idx++;
      let eventcb = args["__event__"];
      if (!isFunction(eventcb)) throw "__event__ ";
      args["__event__"] = ns + "." + funcname + ".__event__" + only_idx;
      xengine.bridge.register(args["__event__"], (res) => {
        // __event__ 
        return eventcb(res);
      });
    }
  }
  //  sync, async 
  // aysnc  cb 
  // sync  return 
  return dsbridge.call(jsimoduleId + "." + funcname, args, cb);
}

function broadcastOff() {
  xengine.bridge.unregister("com.zkty.module.engine.broadcast");
}

function broadcastOn(eventcb) {
  //use("com.zkty.module.engine","broadcast")
  xengine.bridge.register("com.zkty.module.engine.broadcast", (res) => {
    return eventcb(res);
  });
}
let only_idx = 0;

function use(ns, funcs) {
  if (module_names.has(ns)) {
    throw ns + ',,,xengine.use("' + ns + '") ;';
  }
  module_names.add(ns);
  console.log(ns + ",js ");

  let _call = function (funcname, args) {
    if (args.hasOwnProperty("__event__")) {
      only_idx++;
      let eventcb = args["__event__"];
      if (!isFunction(eventcb)) throw "__event__ ";
      args["__event__"] = ns + "." + funcname + ".__event__" + only_idx;
      xengine.bridge.register(args["__event__"], (res) => {
        return eventcb(res);
      });
    }

    if (funcname.startsWith("sync")) {
      return xengine.bridge.call(ns + "." + funcname, args);
    } else {
      let p = new Promise((resolve, reject) => {
        const warning_msg =
          "x-engine 0.1.0  promise,__ret__,.";
        console.warn(warning_msg);
        xengine.bridge.call(ns + "." + funcname, args, function (res) {
          // only resolve once
          resolve(res);
          if (args["__ret__"]) {
            return args["__ret__"](res);
          }
        });
      });
      return p;
    }
  };

  return funcs.reduce((acc, cur, i) => {
    if (isObject(cur)) {
      acc[cur.name] = (args) =>
        _call(cur.name, {
          ...cur.default_args,
          ...args
        });
    } else if (isString(cur)) {
      acc[cur] = (args) => _call(cur, args);
    } else {
      throw " string  {name:xxx, default_args:{...}}";
    }
    return acc;
  }, {});
}

Object.defineProperty(xengine, "bridge", {
  get() {
    return dsbridge;
  },
  set: function () {
    throw "dsbridge";
  },
});

function platform() {
  var ua = navigator.userAgent,
    isAndroid = /(?:Android)/.test(ua),
    isPhone = /(?:iPhone)/.test(ua),
    isPc = !isPhone && !isAndroid;
  return {
    isPhone: isPhone,
    isAndroid: isAndroid,
    isPc: isPc,
  };
}

// 
function listenKeybord($input) {
  if (this.platform.isPhone) {
    // IOS IOS  Android 
    $input.addEventListener(
      "focus",
      function () {
        console.log("IOS ");
        // IOS 
      },
      false
    );

    // IOS IOS 
    $input.addEventListener("blur", () => {
      console.log("IOS ");
      // IOS 
    });
  }

  // Andriod Andriod 
  if (this.platform.isAndroid) {
    var originHeight =
      document.documentElement.clientHeight || document.body.clientHeight;

    window.addEventListener(
      "resize",
      function () {
        var resizeHeight =
          document.documentElement.clientHeight || document.body.clientHeight;
        if (originHeight < resizeHeight) {
          console.log("Android ");
          // Android 
        } else {
          console.log("Android ");
          // Android 
        }

        originHeight = resizeHeight;
      },
      false
    );
  }
}

var $inputs = document.querySelectorAll(".input");

for (var i = 0; i < $inputs.length; i++) {
  listenKeybord($inputs[i]);
}

patch.disableDoubleTapScroll = function (ms) {
  ms = ms || 500;
  console.log("," + ms + " ms");
  //, webview 
  //, 
  var agent = navigator.userAgent.toLowerCase();
  var iLastTouch = null;
  if (agent.indexOf("iphone") >= 0 || agent.indexOf("ipad") >= 0) {
    document.body.addEventListener(
      "touchend",
      function (event) {
        var a = new Date().getTime();
        iLastTouch = iLastTouch || a + 1;
        var c = a - iLastTouch;
        if (c < ms && c > 0) {
          event.preventDefault();
          return false;
        }
        iLastTouch = a;
      },
      false
    );
  }
};
export default xengine;